/* ProductList.css */

.product-list-container {
  padding: 20px;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f9f9f9;
  min-height: 100vh;
   background: linear-gradient(to right, #f2fbff, rgb(92, 191, 244)); 
   border-radius: 12px;
}

/* âœ… Top Section: Heading + Add Button */
.product-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.product-heading {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

/* âœ… Add Product Button */
.btn-add-product {
  background-color: #0d7fe3;
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.btn-add-product:hover {
  background-color: green;
}

/* âœ… Table Wrapper */
.table-wrapper {
  overflow-x: auto;
}

.product-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  min-width: 600px;
}

.product-table th,
.product-table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.product-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.btn-edit,
.btn-delete {
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s;
}

.btn-edit {
  background-color: #007bff;
  color: white;
}

.btn-edit:hover {
  background-color: #0056b3;
}

.btn-delete {
  background-color: #dc3545;
  color: white;
}

.btn-delete:hover {
  background-color: #a71d2a;
}

/* âœ… Responsive Design */
@media (max-width: 600px) {
  .product-list-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .product-heading {
    font-size: 20px;
    text-align: left;
  }

  .btn-add-product {
    width: 100%;
    text-align: center;
  }

  .btn-edit,
  .btn-delete {
    flex: 1 1 100%;
    padding: 8px;
    font-size: 14px;
  }

  .product-actions {
    flex-direction: column;
    align-items: stretch;
  }
}
