/* login.css */
.login-container {
  position: fixed;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  width: 100%;
  padding: 20px;
  font-family: 'Segoe UI', sans-serif;
}

.login-heading {
  text-align: center;
  font-size: 22px;
  color: #2c3e50;
  margin-bottom: 20px;
}

.login-box {
  max-width: 340px;
  width: 100%;
  background: linear-gradient(to right, #f2fbff, rgb(4, 160, 244));
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #ddd;
}

.login-inner {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-title {
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 10px;
}

.role-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.role-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  background-color: #126dd5;
  cursor: pointer;
}

.role-btn:hover {
  opacity: 0.9;
}

.active-admin {
  background-color: #2563eb;
  color: white;
}

.active-user {
  background-color: #16a34a;
  color: white;
}

.role-separator {
  color: #aaa;
  font-weight: bold;
  
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.login-form input {
  padding: 8px 10px;
  margin-bottom: 8px;
  width: 85%;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.login-form button {
  background-color: #007bff;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  width: 85%;
}

h5{
  text-align: center;
  color: black;
}

/* Responsive */
@media (max-width: 480px) {
  .login-container {
    padding: 10px;
  }

  .login-heading {
    font-size: 18px;
  }

  .login-title {
    font-size: 18px;
  }

  .role-btn {
    padding: 6px 12px;
    font-size: 14px;
  }
}

body {
  background: #f2fbff;
  min-height: 100vh;
}
