.invoice-history-container {
  max-width: 1000px;
  margin: 30px auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(to right, #f2fbff, rgb(92, 191, 244));
}

.invoice-history-container h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

.search-container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 25px;
}

.search-box {
  width: 100%;
  max-width: 300px;
  padding: 10px 14px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 15px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.05);
}

.invoice-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
  margin-top: 10px;
}

.invoice-table th,
.invoice-table td {
  padding: 12px 10px;
  border: 1px solid #ddd;
  text-align: center;
}

.invoice-table th {
  background-color: #f0f0f0;
  color: #333;
  font-weight: 600;
  text-transform: uppercase;
}

.invoice-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.invoice-table tr:hover {
  background-color: #f1f1f1;
}

/* ðŸ”½ Expand Button */
.expand-btn {
  padding: 6px 12px;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  font-size: 13px;
}

.expand-btn:hover {
  background-color: #0056b3;
}

/* ðŸ§¾ Product Sub-table */
.product-sub-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  background-color: #ffffff;
}

.product-sub-table th,
.product-sub-table td {
  border: 1px solid #ddd;
  padding: 8px 10px;
  text-align: center;
}

.product-sub-table th {
  background-color: #e9ecef;
  font-weight: 600;
  color: #333;
}

.product-row td {
  background-color: #fefefe;
  padding: 0;
}

/* ðŸ“± Responsive Layout */
@media (max-width: 768px) {
  .search-container {
    justify-content: center;
  }

  .invoice-table,
  .invoice-table thead,
  .invoice-table tbody,
  .invoice-table th,
  .invoice-table td,
  .invoice-table tr {
    display: block;
  }

  .invoice-table thead tr {
    display: none;
  }

  .invoice-table tr {
    margin-bottom: 15px;
    border-bottom: 2px solid #ccc;
  }

  .invoice-table td {
    text-align: right;
    padding-left: 50%;
    position: relative;
  }

  .invoice-table td::before {
    content: attr(data-label);
    position: absolute;
    left: 10px;
    width: 45%;
    padding-left: 10px;
    font-weight: bold;
    text-align: left;
  }

  .product-sub-table {
    font-size: 14px;
  }

  .expand-btn {
    width: 100%;
    margin-top: 8px;
  }
}
